import Vue from 'vue'
import Vuex from 'vuex'
import Localbase from 'localbase'

let db = new Localbase('db')
db.config.debug = false

Vue.use(Vuex)

export default new Vuex.Store({

  state: {
    updates: [],
    newData: null,
    last_check: ''
  },

  mutations: {
    addUpdate(state, newUpdate) {
      state.updates.push(newUpdate)
    },
    setUpdates(state, updates) {
      state.updates = updates
    },
    clearUpdates(state) {
      state.updates = []
    },
  },

  actions: {
    addUpdate({ commit }, newUpdateData) {
      var now = new Date()
      var today = now.toLocaleDateString()
      var time = now.toLocaleTimeString()
      var newUpdateStock = newUpdateData[0]
      var newUpdateAlert = newUpdateData[1]
      var newUpdateTimeframe = newUpdateData[2]
      let newUpdate = {
        id: Date.now(),
        stock: newUpdateStock,
        alert: newUpdateAlert,
        time_frame: newUpdateTimeframe,
        time: today + ' @ ' + time
      }
      db.collection('updates').add(newUpdate).then(()  => {
        commit('addUpdate', newUpdate)
      })
    },
    clearUpdates({ commit }) {
      db.collection('updates').delete()
      commit('clearUpdates')
    },
    setUpdates({ commit }, updates) {
      db.collection('updates').set(updates)
      commit('setUpdates', updates)
    },
    getUpdates({ commit }) {
      db.collection('updates').get().then(updates => {
        if (updates.length === 0) {
          // let updateOne = {
          //   id: 1,
          //   stock: 'QQQ',
          //   alert: 'STRONG LONG ENTRY',
          //   time_frame: 'small',
          //   time: '12:01:30'
          // }
          
          // db.collection('updates').add(updateOne).then(()  => {
          //   commit('addUpdate', updateOne)
          // })
        }
        else {
          commit('setUpdates', updates)
        }
      })
    },
  },
  modules: {
  },
  getters: {
    updatesAll(state) {
        return state.updates.reverse()
    }
  }
})
