<script>
import axios from 'axios';

export default {
  name: 'Home',

  data: () => ({
    actual: [],
    past_entries: [      
    ],
    last_check: '',
    time: '',
    new_data: null,
    line1: {
      small_stock: '',
      small_alert: '',
      med_stock: '',
      med_alert: '',
      lg_stock: '',
      lg_alert: ''
    },
    line2: {
      small_stock: '',
      small_alert: '',
      med_stock: '',
      med_alert: '',
      lg_stock: '',
      lg_alert: ''
    },
    line3: {
      small_stock: '',
      small_alert: '',
      med_stock: '',
      med_alert: '',
      lg_stock: '',
      lg_alert: ''
    }
  }),
  props: {
  },
  mounted () {
    this.$store.dispatch('getUpdates')
    // check time to see if  between 6:30 and 1
    // if outside of time period break and don't update
    var now = new Date()
    var day = now.getDay()
    var today = now.toLocaleDateString()
    var time = now.toLocaleTimeString()
    this.time = today + ' @ ' + time;
    var hour = now.getHours()
    if (day == 0 || day == 6) {
      this.last_check = 'Friday @ 1:00 PM PST'
    } else if (hour <= 6 || hour >= 13){
      this.last_check = 'Today @ 1:00 PM PST'
    } else {
      this.dataGrab()
      setInterval( this.dataGrab, 60000);
    }
  },
  filters: {
    notavailable (value) {
      if(value == '#N/A'){
        return ''
      } else {
        return value
      }
    }
  },
  methods: {
    clearUpdates: function(){
      this.$store.dispatch('clearUpdates')
    },
    naCheck (val) {
      if (val === '' || val === '#N/A') return false
      else return true
    },
    dataGrab: function () {
      var now = new Date()
      var hour = now.getHours()
      if (hour >= 13) {
        this.last_check = '1:00 PM PST'
        clearInterval()
      } else {
        var today = now.toLocaleDateString()
        var time = now.toLocaleTimeString()
        this.last_check = today + ' @ ' + time;


        axios.get('https://spreadsheets.google.com/feeds/cells/1mNcDRllfcQdq02F8h1xaFoGM8Es_4mpFvE61h5N88kY/1/public/full?alt=json')
        .then((response) => (
          this.new_data = response.data.feed.entry,
          
          this.line1.small_stock = this.new_data[6].gs$cell.$t,
          this.line1.small_alert = this.new_data[7].gs$cell.$t,
          this.line2.small_stock = this.new_data[12].gs$cell.$t,     
          this.line2.small_alert = this.new_data[13].gs$cell.$t,
          this.line3.small_stock = this.new_data[18].gs$cell.$t,     
          this.line3.small_alert = this.new_data[19].gs$cell.$t,
          


          this.line1.med_stock = this.new_data[8].gs$cell.$t,
          this.line1.med_alert = this.new_data[9].gs$cell.$t,
          this.line2.med_stock = this.new_data[14].gs$cell.$t,
          this.line2.med_alert = this.new_data[15].gs$cell.$t,
          this.line3.med_stock = this.new_data[20].gs$cell.$t,
          this.line3.med_alert = this.new_data[21].gs$cell.$t,


