      if (this.line1.small_stock !== ('#N/A' || '')) {
        var updateDate = [
          this.line1.small_stock,
          this.line1.small_alert,
          'short'
        ]
        // append new data to localbase
        this.$store.dispatch('addUpdate', updateDate)

      } else {
