          this.line3.med_alert,
          'medium'
        ]
        // append new data to localbase
        this.$store.dispatch('addUpdate', updateDate)
      } else {          
        // pass
      }
    },
    'line1.lg_stock': function () {
      if (this.line1.lg_stock !== ('#N/A' || '')) {
